		<view class="article">
			<text>  今年以来，随着疫情防控在乡村的全面落实，各种形之有效卫生健康普及宣传，结合美丽乡村建设、农村垃圾分类宣传、推进取得明显成效，使广大村民环保意识明显增强了，村民对于垃圾分类意识也同步提高。垃圾分类是一个系统工程，更是一项造福于子孙后代的民生福祉工程。推动农村生活垃圾分类处理，完善农村生活垃圾分类处理，促进垃圾分类和资源化利用建立，是建设美丽乡村的必由之路。下一步，就是要把他们认可的想法逐渐转变为生活习惯，来推动农村垃圾分类。当前，全县正大力推进生活垃圾分类向基层农村推进，做好农村垃圾分类也是重要工程。结合当前驻村现状形势，就乡村垃圾分类治理的工作提出一点思考：

一、农村垃圾分类的现状

所驻行政村下辖4个自然村，其中二个自然小村在山里，村人口约2300余人，约820余户。垃圾分类采用网格化管理，“两委”干部、党员分片包干，监督垃圾分类执行情况。目前，农村生活垃圾主要以厨余垃圾和易腐蚀垃圾为主，但近些年随着生活水平提高，农村生活垃圾产生量迅速增加，农村生活垃圾的成分变得多样化，其他如塑料袋、泡沫盒、玻璃、金属、等生活垃圾比例攀升，处理变得困难。建筑垃圾呈上升趋势，随意丢弃或露天堆放、废弃农药瓶内残留农药等现象存在，容易污染周边的土地和水体环境，容易滋生蚊蝇，破坏了水资源、土壤、空气，破坏美丽乡村自然景观,影响居民生活和农业生产。

二、当前垃圾分类工作中存在的问题

一是村民的垃圾分类意识还有待增强。当前农村生活垃圾分类减量处理工作启动时间不长，宣传发动工作刚开始，群众基础不够扎实，对垃圾分类工作的认识还不到位，一些村民垃圾分类知识普及不够，村民自己动手垃圾分类能力有待提高，行为习惯尚未真正养成。二是垃圾收运规范化水平还有待提升。在垃圾收运工作开展过程中，部分村级保洁员自身分类意识不强，工作不到位，容易造成一些保洁人员将已进行垃圾分类的厨余垃圾、有害垃圾再混收混运，存在着“先分后混”的尴尬局面。三是垃圾分类在村级事务中管理高度不够。目前村级组织尽管有参与的热情且在每周工作例会中得到体现与会商，但尚未将垃圾分类纳入组织章程中，缺少长效机制保障。造成经常会商，缺少具体举措和落实的措施。处于推动下显成效，不推回原点的状态。四是垃圾分类的参与主体还有待扩增。垃圾分类工作尚处在起步阶段，主要还是靠政府在推动，村级组织落实。而垃圾分类各个环节需要更多党员、村民代表、社会参与，虽采取分片包干监督的网格化管理，但在执行过程中存在时紧时松现象。目前从事垃圾分类的专业企业还比较少，市场化运作还有一定的操作难度。村级组织中的作用显现不够突出。五是村级组织在实施过程中缺乏足够的资金支持。由于村集体经济薄弱，收入来源匮乏。专门用于垃圾分类的资金得不到充分保障。在人员安排、设施保障、后期处理等方面滞后于垃圾分类现状，使垃圾分类不能有效推进。

三、推进农村垃圾分类的几点建议

1、日常生活垃圾几种分类方法。农村生活垃圾分类处理是一项系统工程，需要结合农村实际，统筹考虑农村生活垃圾的利用、处理，探索方式多样的生活垃圾收运处置体系。目前在开展的垃圾分类收集处理工作，通行的基本做法有四法，即分为可回收垃圾、餐厨垃圾、有害垃圾和其他垃圾。有三分法，即可腐烂和不可腐烂、可回收垃圾。也有简单的两分法，即湿垃圾和干垃圾、可回收和不可回收等几种分法。对于一个区域或行政片来讲，统一垃圾的分法或分类标准，有利于管好、利用好垃圾资源的回收利用。当然，对于资源的回收利用，越细分，利用越充分。但对于刚开始接触垃圾分类的群众来说，分类应从简单分类开始，先易后难，逐步细分，给群众一个接受的过程。可以在各自然村先从两分开始，落实垃圾分类指导员，指导他们把垃圾按可腐烂和不可腐烂分类。对农村餐厨垃圾、其他垃圾、装修垃圾（大件垃圾）等实行分流，由保洁员等专业人员收集送至村、镇垃圾分类收集分拣站或有机垃圾处理站，餐厨垃圾进行资源化处理，大件垃圾进行拆解、破碎、分拣和分类。可腐烂的餐厨垃圾通过定点投放、定时收集、定车运输、定点处理，通过机械制肥，达到资源循环利用。不可腐烂的垃圾，通过专用运输车收集后，集中压块，运往焚烧厂作焚烧发电处理。

2、继续加大宣传垃圾分类。进一步加大宣传力度利用，宣传标语和图文并茂的宣传画、墙画，培训说明会、垃圾收集车循环播放垃圾分类知识等多种群众喜闻乐见的形式，让村民详细了解垃圾分类处理的意义，明确具体的分类标准、主要做法和自己所承担的责任与义务，以及先进农户典型做法的宣传。强化农民对农村环保认同感、知晓度。鼓励镇村干部党员以志愿服务名义进村入户，广泛开展“听民情、汇民意”活动，召开各种形式的恳谈会，面对面与农民群众宣传沟通，广聚民意，充分发挥农民积极性、主动性和创造性。

3、加强垃圾分类教育培训。以家庭为单位，建立垃圾分类分层培训制度，对一线垃圾分类指导员、监督员、村民小组长、党员骨干、保洁员分别进行分类培训，增强全民垃圾分类的意识，逐步形成“垃圾分类，从我做起”的良好习惯。在中小学开设资源回收再利用课程，培养学生垃圾分类、资源节约和环境保护意识，逐步改变村民的传统观念，让村民认识到垃圾分类的重要性，引导村民自觉参与。制定切合当地农民实际的农村生活垃圾分类管理规范要求，用制度规范村民行为。垃圾分类不是一朝一夕就能完成的一项工作，它需要村民真正自觉自愿地参与其中，并逐渐养成一种生活习惯，使其成为新的生活方式。同时，在加强宣传的基础上，引进奖励和激励机制，对一些日常垃圾分类做的好的农户采取挂牌“示范户”的形式，给予一定的物质奖励。对一些进步明显并在村中积极争做垃圾分类志愿者的人、户给予适当物质奖励。形成在村中争做垃圾分类的先进户。使垃圾分类在村中有序推进。

4、探索分类处理第三方服务模式。在农村生活垃圾分类减量处理上，进一步探索第三方服务，进行“政府主导、公开招标、合同管理、评估兑现”第三方服务的管理模式。通过市场竞标，引入竞争机制，成立美丽乡村环境服务有限公司。政府部门抓管理，环境服务有限公司具体做，彻底解决农村生活垃圾分类“易反复、常反弹”的困扰，提高农村生活垃圾分类减量处理质量和群众满意度。同时也减轻村在资金上的投入。

5、发挥党员干部带头作用。要注重发挥农村党员先锋模范作用，把环境整治等攻坚克难的农村工作与基层党建工作结合起来，选聘鼓励镇村干部进村入户义务监督员，完善农村生活垃圾分类减量处理、长效保洁、公用设施管护维护制度。积极开展党员干部“垃圾分类、美化家园”义务劳动活动。同时，将党员联户帮扶工作与日常垃圾分类的宣传、监督工作有机结合起来，使党员联户工作的内容更加宽泛，责任更加明确。引导农民积极参与农村生活垃圾分类减量处理。宣传发动、邻里互动，进一步激发广大农民群众农村生活垃圾分类减量处理的热情。</text>
